import { Injectable } from '@angular/core';
import { Resolve, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';
import { Observable } from 'rxjs';

import { SearchService, SearchType } from './search.service';

@Injectable()
export class KeywordsSearchResolver implements Resolve<any> {

  constructor(private searchService: SearchService) {}

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any> {
    const keywords: string = route.params.keywords;
    const type: string = route.params.type;
    let t: SearchType;
    if (type === 'documents') {
      t = SearchType.Documents;
    } else if (type === 'news') {
      t = SearchType.News;
    } else if (type === 'people') {
      t = SearchType.People;
    } else if (type === 'requests') {
      t = SearchType.Requests;
    } else {
      t = SearchType.Simple;
    }
    return this.searchService.searchByType(t, keywords);
  }
}
