import {
  Component, OnInit, AfterViewInit, ElementRef, ViewChild, NgZone, OnDestroy
} from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { fromEvent, from, Subscription } from 'rxjs';
import { map, distinctUntilChanged, debounceTime } from 'rxjs/operators';

import { SearchType, SearchService } from '../../services/search/search.service';
import { SidebarService } from 'src/app/services/sidebar/sidebar.service';
import { PortalService } from 'src/app/services/portal/portal.service';

@Component({
  selector: 'app-search-results-page',
  templateUrl: './search-results-page.component.html',
  styleUrls: ['./search-results-page.component.scss']
})
export class SearchResultsPageComponent implements OnInit, AfterViewInit, OnDestroy {
  public showSidebar: Boolean = false;
  public type: SearchType = null;
  public keywords: string = null;
  public filter: string = null;
  @ViewChild('searchRef') searchRef: ElementRef;

  public hasPrev = false;
  public hasNext = false;
  public result: any;

  private pages: any[] = [];
  private index: number = null;
  private keySubscription: Subscription = null;
  private keywordSubscription: Subscription = null;
  private resultSubscription: Subscription = null;

  constructor(
    private route: ActivatedRoute,
    private sidebarService: SidebarService,
    private sharepointService: PortalService,
    private searchService: SearchService,
    private zone: NgZone,
    private router: Router
  ) {
    this.sidebarService.handleSetSettings({ showSidebar: false, bgColor: '#F7F7F7' });
  }

  filtersIsVisible: Boolean = false;

  handleToggleFilters() {
    this.filtersIsVisible = !this.filtersIsVisible;
  }

  ngOnInit() {
    this.keywordSubscription = this.route.params
      .pipe(map(data => data.keywords))
      .subscribe(keywords => (this.keywords = keywords));
      this.keywordSubscription = this.route.params
      .pipe(map(data => data.type))
      .subscribe(type => {
        if (type === 'documents') {
          this.type = SearchType.Documents;
        } else if (type === 'news') {
          this.type = SearchType.News;
        } else if (type === 'people') {
          this.type = SearchType.People;
        } else if (type === 'requests') {
          this.type = SearchType.Requests;
        } else {
          this.type = SearchType.Simple;
        }
      });
    this.resultSubscription = this.route.data
      .pipe(map(data => data.result))
      .subscribe(result => this.initSearchResults(result));
  }

  ngAfterViewInit(): void {
    this.keySubscription = fromEvent(this.searchRef.nativeElement, 'keyup')
      .pipe(
        map((e: any) => e.target.value),
        debounceTime(1000),
        distinctUntilChanged()
      ).subscribe((query: string) => {
        if (query && query !== '') {
          this.searchService.searchByType(this.type, query);
        } else {
          this.pages = [];
          this.result = null;
          this.index = 0;
          this.hasPrev = false;
          this.hasNext = false;
        }
      });
  }

  ngOnDestroy(): void {
    if (this.keywordSubscription != null) {
      this.keywordSubscription.unsubscribe();
    }
    if (this.keySubscription != null) {
      this.keySubscription.unsubscribe();
    }
    if (this.resultSubscription != null) {
      this.resultSubscription.unsubscribe();
    }
  }

  search(type?: SearchType) {
    this.type = type;
    this.searchService.searchByType(this.type, this.keywords)
      .subscribe(result => this.initSearchResults(result));
  }


  onKeypress(event) {
    if (event.key === 'Enter') {
      this.searchService.searchByType(this.type, this.keywords)
        .subscribe(result => this.initSearchResults(result));
    }
  }

  getNext() {
    if (this.hasNext) {
      if (++this.index === (this.pages.length)) {
        from(this.result.getPage(this.index + 1))
          .subscribe((page: any) => {
            this.result = page;
            this.hasPrev = true;
            this.pages.push(page);
            let total = 0;
            this.pages.forEach(p => total += p.RowCount);
            this.hasNext = page.TotalRows > total;
          });
      } else {
        this.result = this.pages[this.index];
        let total = 0;
        this.pages.slice(0, this.index + 1).forEach(p => total += p.RowCount);
        this.hasNext = this.result.TotalRows > total;
        this.hasPrev = true;
      }
    }
  }

  getPrev() {
    if (this.hasPrev) {
      this.index--;
      this.result = this.pages[this.index];
      this.hasNext = true;
      this.hasPrev = this.index > 0;
    }
  }

  getType(item: any): string {
    const documents = ['docx', 'doc', 'xlsx', 'xls', 'pdf'];
    if (documents.indexOf(item.FileExtension) >= 0) {
      return 'Документы';
    }
    if (item.OriginalPath && item.OriginalPath.indexOf('/Lists/slNews/') > 0) {
      return 'Новости';
    }
    if (item.OriginalPath && item.OriginalPath.indexOf('/Lists/slEmployees/') > 0) {
      return 'Люди';
    }
    return 'Все';
  }

  getLink(item: any): string {
    const documents = ['docx', 'doc', 'xlsx', 'xls', 'pdf'];
    if (documents.indexOf(item.FileExtension) >= 0) {
      return item.OriginalPath;
    }
    if (item.OriginalPath
        && item.OriginalPath.indexOf('/Lists/slNews/') > 0
        && item.OriginalPath.indexOf('?ID=') > 0) {
      const id = item.OriginalPath.split('?ID=')[1];
      return `${window.location.protocol}//${window.location.host}`
        + `${this.sharepointService.getCurrentUserContext().webServerRelativeUrl}/PortalPages/home.aspx#/news/${id}`;
    }
    if (item.OriginalPath && item.OriginalPath.indexOf('/Lists/slEmployees/') > 0
      && item.OriginalPath.indexOf('?ID=') > 0) {
      const id = item.OriginalPath.split('?ID=')[1];
      return `${window.location.protocol}//${window.location.host}`
        + `${this.sharepointService.getCurrentUserContext().webServerRelativeUrl}/PortalPages/home.aspx#/phonebook/${id}`;
    }
    if (item.OriginalPath && item.OriginalPath.indexOf('/Lists/slVideos/') > 0
      && item.OriginalPath.indexOf('?ID=') > 0) {
      const id = item.OriginalPath.split('?ID=')[1];
      return `${window.location.protocol}//${window.location.host}`
        + `${this.sharepointService.getCurrentUserContext().webServerRelativeUrl}/PortalPages/home.aspx#/media/video/item/${id}`;
    }
    return item.OriginalPath;
  }

  formatDescription(result: any) {
    return result.HitHighlightedSummary.replace(/c0/gi, 'strong').replace(/<ddd\/>/gi, '&#8230;');
  }

  goToUrl(event, link: string) {
    event.stopPropagation();
    this.zone.run(() => {
      if (window.location.href.indexOf('/PortalPages/') > 0
        && link.indexOf('/PortalPages/') >= 0
        && link.indexOf('#') >= 0) {
        this.router.navigate([link.split('#')[1]]);
      } else {
        window.location.href = link;
      }
    });
  }

  private initSearchResults(result: any) {
    this.pages = [];
    this.pages.push(result);
    this.result = result;
    this.index = 0;
    this.hasPrev = false;
    this.hasNext = this.result.TotalRows > this.result.RowCount;
  }
}
